import React, { Component } from 'react';
import './App.css';
import './nprogress.css';
import { 
  getEvents, 
  // getAccessToken, 
  // extractLocations, 
  // getToken, 
  checkToken
} from './api';

import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';

class App extends Component {
  state = {
    events: [],
    locations: [],
    locationSelected: 'all',
    numberOfEvents: 32,
    tokenCheck: false
  }

  async componentDidMount() {
    const accessToken = localStorage.getItem('access_token');
    // The value of validToken is saying that if there is a token, 
    // use checkToken(accessToken) to make make sure it is valid.
    // If checkToken(accessToken) returns with an invalid token,
    // then the value of validToken will be false.
    const validToken = accessToken !== null ? await checkToken(accessToken) : false;
    console.log('validToken: ', validToken);
    console.log('Access_Token: ', accessToken);

    this.setState({ tokenCheck: validToken });

    if(validToken === true) this.updateEvents();

    const searchParams = new URLSearchParams(window.location.search);
    const code = searchParams.get('code');

    this.mounted = true;
    if (code && this.mounted === true && validToken === false) {
      this.setState({ tokenCheck: true });
      this.updateEvents();
    }
    // getEvents().then((events) => {
    //   if (this.mounted) {
    //     this.setState({ events, locations: extractLocations(events) });
    //   }
    // });
  }

  getData = () => {
    const { locations, events } = this.state;
    const data = locations.map((location) => {
      const number = events.filter((event) => event.location === location)
        .length;
      const city = location.split(" ").shift();
      return { city, number };
    });
    return data;
  };

  componentWillUnmount() {
    this.mounted = false;
  }

  updateEvents = (location, eventCount) => {
    const { locationSelected, numberOfEvents } = this.state;

    if (location) {
      getEvents().then((response) => {
        const locationEvents = 
          location === 'all'
            ? response.events
            : response.events.filter((event) => event.location === location);
        const events = locationEvents.slice(0, numberOfEvents);
        return this.setState({
          events: events,
          locationSelected: location,
          locations: response.locations,
        });
      });
    } else {
      getEvents().then((response) => {
        const locationEvents = 
          locationSelected === 'all'
            ? response.events
            : response.events.filter(
              (event) => event.location === locationSelected
            );
        const events = locationEvents.slice(0, eventCount);
        return this.setState({
          events: events,
          numberOfEvents: eventCount,
          locations: response.locations,
        });
      });
    }
    // getEvents().then((events) => {
    //   const locationEvents = (location === 'all') ?
    //   events :
    //   events.filter((event) => event.location === location);
    //   this.setState({
    //     events: locationEvents
    //   });
    // });
  };

  render() {
    const { locations, events } = this.state;
    return (
      <div className="App">
        <CitySearch 
          locations={locations}
          updateEvents={this.updateEvents}
        />
        <EventList 
          events={events} 
        />
        <NumberOfEvents 
          updateEvents={this.updateEvents} 
        />
      </div>
    )
  }
}

export default App;
